import { User } from './User'; 

export class Message {

    private _from: User;
	private _to: User;
	
    private _time: string;
    private _text: string;

	constructor(from, to, time, text) {
		this._from = from;
		this._to = to;
		this._time = time;
		this._text = text;
	}

	public get from(): User {
		return this._from;
	}

	public set from(value: User) {
		this._from = value;
	}

	public get to(): User {
		return this._to;
	}

	public set to(value: User) {
		this._to = value;
	}

	public get time(): string {
		return this._time;
	}

	public set time(value: string) {
		this._time = value;
	}

	public get text(): string {
		return this._text;
	}

	public set text(value: string) {
		this._text = value;
	}
    
}



