import { Component, OnInit, ViewChild, ElementRef } from '@angular/core';

import { Message } from '../../models/Message';
import { User } from '../../models/User';

@Component({
  selector: 'app-main',
  templateUrl: './main.component.html',
  styleUrls: ['./main.component.scss']
})
export class MainComponent implements OnInit {
  @ViewChild('inputUser')
  private inputUser: ElementRef;
  
  @ViewChild('chatList')
  private chatList: ElementRef;
  
  
  private messages: Array<Message> = [];
  
  private bot:User = new User("bot","bot.jpg");
  private me:User = new User("me","me.jpg");
  
  constructor() {
    this.messages.push(new Message(this.bot, this.me, this.getTime(), "hello, i'm a bot!! Enter a number between 1 and 10"));
  }
  
  ngOnInit() {
  }
  
  onEnter(value: string) {
    if (value.length){
      
      this.messages.push(new Message(this.me, this.bot, this.getTime(), value));
      this.inputUser.nativeElement.value = "";
      
      setTimeout(() => {
        this.processMessage(value);
      }, 100);
      

      this.scrollToBottom();
    }
  }
  
  scrollToBottom(): void {
    setTimeout(() => {
      try {
        this.chatList.nativeElement.scrollTop = this.chatList.nativeElement.scrollHeight + 60;
      } catch(err) { }                 
    }, 10);
  }
  
  processMessage(message){

    let msgs = [
      "message 1",
      "message 2",
      "message 3",
      "message 4",
      "message 5",
      "message 6",
      "message 7",
      "message 8",
      "message 9",
      "message 10"
      
    ];

    let randomMsgs = [
      "Hail a taxi",
      "Order food delivery",
      "Buy movie tickets",
      "Customize and order a pair of Nikes",
      "Send an order to the nearest Starbucks",
      "Track your daily fitness progress",
      "Shop Burberry’s latest collection",
      "Book doctor’s appointments",
      "Host a business conference call",
      "Pay your water bill"
    ];

    randomMsgs.sort(function() {
      return .5 - Math.random();
    });

    if (+message && message >= 1 && message <= 10){//is number
      this.messages.push(new Message(this.bot, this.me, this.getTime(), msgs[message-1]));
    } else {
      this.messages.push(new Message(this.bot, this.me, this.getTime(), randomMsgs[0]));
    }
    this.scrollToBottom();
  }
  
  getTime()  {
    let date = new Date();
    let hours = date.getHours();
    let minutes:any = date.getMinutes();
    let ampm = hours >= 12 ? 'PM' : 'AM';
    hours = hours % 12;
    hours = hours ? hours : 12; // the hour '0' should be '12'
    minutes = minutes < 10 ? '0'+minutes : minutes;
    var strTime = hours + ':' + minutes + ' ' + ampm;
    return strTime;
  }            
  
  
}



