export class User {
    
    private _name: string;
    private _image: string;

	constructor(name, image) {
		this._name = name;
		this._image = image;
	}
	
	public get name(): string {
		return this._name;
	}
    
	public set name(value: string) {
		this._name = value;
	}
    
	public get image(): string {
		return this._image;
	}
    
	public set image(value: string) {
		this._image = value;
	}
    
}



